

import { motion } from 'framer-motion';

const containerVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: { 
        opacity: 1,
        y: 0,
        transition: {
            duration: 0.5,
            staggerChildren: 0.2,
        }
    }
}

const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
        opacity: 1,
        y: 0,
        transition: {
            duration: 0.5,
            ease: 'easeOut',
        }
    }
}

const FeaturesSection = () => {
  return (
    <motion.div
        variants={containerVariants}
        whileInView='visible'
        viewport={{ once: true }}
        initial='hidden'
        className='py-22 px-6 sm:px-8 lg:px-12 xl:px-16 bg-white'
    >
        <h2 className='text-center text-2xl font-bold'>
            ویژگی های راه خانه تو
        </h2>
    </motion.div>
  )
}

export default FeaturesSection